import React from 'react';
import {Link} from 'react-router-dom';

class SideBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      links: [
        {label: "Dashboard", url: "dashboard"},
        {label: "Workers", url: "workers"},
        {label: "Zones", url: "zones"},
        {label: "Reports", url: "reports"},
        {label: "Manage Site", url: "site"},
        {label: "Settings", url: "settings"},
      ]
    };
  }

  renderLinks() {
    this.state.links.map((link) => {
      return (
        <li className="sidebar-links__item">
          <Link activeClassName="is-active" className="sidebar-link" to={link.url}>
            {link.label}
          </Link>
        </li>
      )
    })
  }

  render() {
    const { admin }  = this.props
    return (
      <nav className="sidebar" id="sidebar">
        <div className="sidebar-header">
          <Link className="brand-logo" to="/">
            <img src="https://static1.squarespace.com/static/59ed9589e45a7c27e9400b3c/t/5a241aa5e2c483bcf3cf4287/1541907203250/?format=1500w" alt="WakeCap Technologies"/>
          </Link>
        </div>
        <ul className="list-unstyled components sidebar-links">
          <li className="sidebar-links__item">
            <Link activeClassName="is-active" className="sidebar-link" to="/dashboard">
              Dashboard
            </Link>
          </li>
          <li className="sidebar-links__item">
            <Link className="sidebar-link" to="/workers">
              Workers
            </Link>
          </li>
          <li className="sidebar-links__item">
            <Link className="sidebar-link" to="/zones">
              Zones
            </Link>
          </li>
          <li className="sidebar-links__item">
            <Link className="sidebar-link" to="/reports">
              Reports
            </Link>
          </li>
          <li className="sidebar-links__item">
            <Link className="sidebar-link" to="/site">
              Manage Site
            </Link>
          </li>
          <li className="sidebar-links__item">
            <Link className="sidebar-link" to="/setting">
              Settings
            </Link>
          </li>
        </ul>
      </nav>
    );
  }
}


export default SideBar;
